body {
  font-family: "Poppins", sans-serif;
}

.header_wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 18px 0;
}

.logo {
  display: block;
  width: 190px;
  height: 84px;
  background: url("../img/logo.svg") no-repeat center/contain;
  order: 1;
}

input::-webkit-input-placeholder {
  color: #A8A8A8;
  font-size: 15px;
}

input:-moz-placeholder {
  color: #A8A8A8;
  font-size: 15px;
}

input:-ms-input-placeholder {
  color: #A8A8A8;
}

.search_block {
  display: flex;
  align-items: center;
  margin: 0 10px;
  flex-shrink: 1;
  order: 2;
}

.search_form {
  display: flex;
}

.search_input {
  background: #F9F9F9;
  height: 50px;
  font-size: 15px;
  border: none;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  padding: 0 15px;
}

.search_button {
  border: none;
  background: #82BA01 url("../img/search.svg") no-repeat center;
  border-radius: 0 6px 6px 0;
  width: 54px;
  position: relative;
}

.branch_select_block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding-left: 58px;
  margin-right: 10px;
  order: 3;
  &::before {
    display: block;
    content: '';
    width: 48px;
    height: 48px;
    top: 10px;
    left: 0;
    position: absolute;
    border-radius: 50%;
    background: #F9F9F9 url("../img/location_arrow.svg") no-repeat center;
  }
}

.branch_text {
  font-size: 15px;
  line-height: 25px;
  color: #A8A8A8;
}

.__select {
  position: relative;
  width: 160px;
  height: 40px;
  margin: 0 auto;
  font-size: 15px;
  display: flex;

  &[data-state="active"] {
    .__select__title__arrow {
      &::before {
        transform: translate(-3px, -50%) rotate(-45deg);
      }

      &::after {
        transform: translate(3px, -50%) rotate(45deg);
      }
    }

    .__select__content {
      opacity: 1;
    }

    .__select__label {
      max-height: 28px;
      border-top-width: 1px;
    }
  }
}

.__select__title {
  display: flex;
  align-items: center;
  font-family: "Poppins", sans-serif;
  font-size: 18px;
  line-height: 30px;
  color: #272727;
  border-radius: 5px;

  cursor: pointer;

  &__arrow {
    position: relative;
    margin-left: 8px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      margin: 0;
      display: block;
      width: 10px;
      height: 2px;

      transition: all 0.3s ease-out;

      background-color: black;

      transform: translate(-3px, -50%) rotate(45deg);
    }

    &::after {
      transform: translate(3px, -50%) rotate(-45deg);
    }
  }

  &:hover {
  }
}

.__select__content {
  position: absolute;
  top: 40px;
  left: -12px;
  font-family: "Poppinsr", sans-serif;
  display: flex;
  flex-direction: column;
  width: calc(100% - 6px);
  box-shadow: 0 18px 18px rgba(0, 0, 0, 0.3);
  background-color: #ffffff;
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;

  transition: all 0.3s ease-out;

  opacity: 0;
  z-index: 21;
}

.__select__input {
  display: none;

  &:checked + label {
  }

  &:disabled + label {
    opacity: 0.6;
    pointer-events: none;
  }
}

.__select__label {
  display: flex;
  align-items: center;
  width: 100%;
  height: 40px;
  max-height: 0;
  padding: 0 12px;

  transition: all 0.2s ease-out;

  cursor: pointer;

  overflow: hidden;

  &:hover {
    color: #272727;
    text-decoration: underline;
  }
}

.phone_block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
  padding-left: 58px;
  margin-right: 10px;
  order: 4;
  &::before {
    display: block;
    content: '';
    width: 48px;
    height: 48px;
    top: 10px;
    left: 0;
    position: absolute;
    border-radius: 50%;
    background: #F9F9F9 url("../img/phone.svg") no-repeat center;
  }

  .phone_number {
    color: #272727;
    white-space: nowrap;
    font-size: 18px;
    line-height: 30px;
  }
}
.icons_wrapper{
  order: 5;
}
.icons_box{
  position: fixed;
  top: 380px;
  right: 50px;
  display: flex;
  z-index: 20;
  flex-direction: column-reverse;
  .header_icon{
    margin-bottom: 10px;
  }
}
.header_icon{
  width: 56px;
  height: 56px;
  border-radius: 50%;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  background: #fff;
  &:hover, &:active{
    svg path{
      stroke: #fff;
    }
    background: #82BA01;
  }
  &.top_burger{
    display: none;
    .cross{
      display: none;
    }
  }
}
.icon_number{
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  position: absolute;
  color: #fff;
  background: #6701BA;
  font-size: 12px;
  right: 5px;
  top: 5px;
  text-align: center;
}
.icon_sum{
  display: block;
  position: absolute;
  font-size: 15px;
  color: #272727;
  width: 100px;
  text-align: end;
  top: 18px;
  right: 100%;
}
.button {
  background: #6701BA;
  display: block;
  height: 50px;
  border-radius: 6px;
  font-size: 18px;
  line-height: 50px;
  color: #FFFFFF;

  &:hover {
    background: #9933EC;
    color: #fff;
    text-decoration: none;
  }

  &.feedback_button {
    padding-left: 84px;
    padding-right: 32px;
    position: relative;
    order: 7;
    span {
      display: block;
      position: absolute;
      width: 54px;
      height: 40px;
      border-right: 1px solid #fff;
      top: 5px;
      left: 0;
      background: url("../img/mail.svg") no-repeat center;
    }
  }

  &.catalog_button {
    padding-left: 84px;
    padding-right: 32px;
    position: relative;
    width: 190px;

    span {
      display: block;
      position: absolute;
      width: 54px;
      height: 40px;
      border-right: 1px solid #fff;
      top: 5px;
      left: 0;
      background: url("../img/button_burger.svg") no-repeat center;
    }
  }
}

.menu_wrapper {
  background: #EDF0F6;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
  }

  .item_menu {
    font-size: 18px;
    color: #272727;
  }
}
.scroll_items{
  display: none;
}
.menu_items{
  flex-grow: 1;
  display: flex;
  justify-content: space-around;
}
.content{
  position: absolute;
  background: #fff;
  width: 100%;
  z-index: 1000;
}
.content_wrapper {
  width: 100%;
  height: 770px;
  display: flex;

}

.jquery-accordion-menu .submenu-indicator {
  float: right;
  line-height: 19px;
  font-weight: normal;
  color: #A8A8A8;
  font-family: FontAwesome;
  font-size: 20px;
}

.jquery-accordion-menu, .jquery-accordion-menu * {
  box-sizing: border-box;
  outline: 0
}

.jquery-accordion-menu {
  width: 330px;
  height: 770px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-top: 30px;
  left: 0;
  top: 0;
  position: relative;
  padding-right: 20px;

  &::-webkit-scrollbar {
    width: 5px;
    background-color: #f9f9fd;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background-color: #82BA01;
  }
}

.jquery-accordion-menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.jquery-accordion-menu > ul > li {
  margin: 20px 0;
}

.jquery-accordion-menu > ul > li > a {
  color: #272727;
  font-size: 18px;
  font-weight: bold;
  line-height: 25px;
  &:hover {
    text-decoration: none;
    color: #6701BA;
  }
}

.jquery-accordion-menu > ul > li > ul.submenu {
  display: none;
}

.desktop_menu {
  flex-grow: 2;
  margin-left: 15px;
  padding-left: 50px;
  padding-top: 50px;
  border-left: 1px solid #EDF0F6;
}

.desktop_submenu_header {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 30px;
  font-weight: bold;
}

.desktop_menu_item {
  height: 100%;

  .d_submenu {
    text-decoration: none;
    height: 100%;
    list-style: none;
    padding-left: 0;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
}

.desktop_menu_item > .d_submenu {

  & > li {
    margin-bottom: 50px;

    & > a {
      color: #272727;
      font-size: 18px;
      line-height: 25px;
      font-weight: bold;
    }

    & > .d_submenu {
      margin-top: 15px;
      & > li {
        margin-bottom: 10px;

        & > a {
          color: #272727;
          font-size: 18px;
          line-height: 30px;
        }
        a {
          &:hover {
            text-decoration: none;
            color: #6701BA;
          }
        }
      }
    }
  }
}
@media (min-width: 1400px) {
  .container{
    max-width: 1320px;
  }
}

@media(max-width: 1200px) {

  .logo{
    width: 167px;
  }
  .search_input{
   flex-grow: 1;
    width: auto;
  }
  .header_wrapper{
    flex-wrap: wrap;
    row-gap: 20px;
  }
  .branch_select_block{
    display: none;
    order: 5;
    margin-right: 0;
  }
  .phone_block{
    display: none;
    order: 6;
    margin-right: 0;
  }
  .search_block{
    flex-grow: 1;
    width: calc(100% - 441px);
    .search_form{
      width: 100%;
      .search_input{
        width: calc(100% - 54px);
      }
    }
  }
  .desktop_menu{
    display: none;
  }
  .menu_items{
    white-space: nowrap;
    overflow: hidden;
    justify-content: flex-start;
    .item_menu{
      margin: 3px 8px;
    }
  }
  .button{
    &.feedback_button{
      display: none;
      order: 7;
      padding-left: 72px;
      padding-right: 20px;
    }
  }

  .icon_sum{
    display: none;
  }
  .icons_wrapper{
    display: flex;
    order:3;
  }
  .scroll_items{
    display: block;
    width: 28px;
    height: 28px;
    border: none;
    margin-left: 8px;
    background: url("../img/points.svg") no-repeat center;
    &:focus{
      outline: none;
    }
  }
  .hidden_menu{
    position: absolute;
    right: 0;
    top: 50px;
    flex-direction: column;
    overflow: visible;
    background: #edf0f6;
    z-index: 500;
    display: none;
    .item_menu{
      margin: 5px 0;
      padding: 0 20px;
    }
  }
  .icons_box{
    position: static;
    flex-direction: row;
    .header_icon{
      margin-bottom: 0;
      margin-right: 10px;
    }
  }
  .header_icon{
      background:#F9F9F9;
    &.top_burger{
      order: 4;
      display: flex;
    }
  }
  .content_wrapper{
    height: auto;
  }
  .jquery-accordion-menu {
    width: 100%;
    overflow-x: visible;
    overflow-y: visible;
    padding: 0;
    border-right: none;
    height: auto;
  }
  .jquery-accordion-menu .submenu-indicator-minus > .submenu-indicator {
    transform: rotate(90deg)
  }
  .jquery-accordion-menu > ul > li  ul.submenu {
    display: none;
  }
  .jquery-accordion-menu ul ul.submenu, .jquery-accordion-menu ul ul.submenu li ul.submenu {
    width: 100%;
    display: none;
    position: static
  }
  .jquery-accordion-menu>ul>li{
    margin: 10px 0;
  }
  .jquery-accordion-menu>ul>li>a{
    font-size: 16px;
  }
  .jquery-accordion-menu ul.submenu li {
    clear: both;
    width: 100%;
  }

  .jquery-accordion-menu ul.submenu li a {
    width: 100%;
    float: left;
    border-top: none;
    color: #272727;
    font-size: 15px;
    font-weight: bold;
    position: relative;
    border-left: solid 6px transparent;
    transition: border .2s linear;
  }

  .jquery-accordion-menu ul ul.submenu li:hover > a {
    border-left-color: #e2e2ec;
    &:hover {
      text-decoration: none;
      color: #6701BA;
    }
  }

  .jquery-accordion-menu ul ul.submenu > li > a {
    padding-left: 25px;
    padding-right: 3px;
  }

  .jquery-accordion-menu ul ul.submenu > li > ul.submenu > li > a {
    padding-left: 50px
  }

  .jquery-accordion-menu ul ul.submenu > li > ul.submenu > li > ul.submenu > li > a {
    padding-left: 75px
  }
  .jquery-accordion-menu ul li:hover > ul.submenu {
    display: none;
  }
  .jquery-accordion-menu ul li ul.submenu li:hover > ul.submenu {
    display: none;
  }
  .jquery-accordion-menu ul li ul.submenu li ul.submenu li:hover > ul.submenu {
    display: none;
  }
  .ink {
    display: block;
    position: absolute;
    background: rgba(0, 0, 0, .3);
    border-radius: 100%;
    transform: scale(0)
  }

  .animate-ink {
    animation: ripple .5s linear
  }

  @-webkit-keyframes ripple {
    100% {
      opacity: 0;
      -webkit-transform: scale(2.5)
    }
  }

  @-moz-keyframes ripple {
    100% {
      opacity: 0;
      -moz-transform: scale(2.5)
    }
  }

  @-o-keyframes ripple {
    100% {
      opacity: 0;
      -o-transform: scale(2.5)
    }
  }

  @keyframes ripple {
    100% {
      opacity: 0;
      transform: scale(2.5)
    }
  }
  #demo-list a {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    white-space: nowrap;
    width: 100%;
    margin: 5px 0;
    padding-right: 3px;
  }
}
@media (max-width: 768px) {
  .header_icon.top_burger{
      order: 3;
  }
  .search_block{
    order: 4;
    width: 100%;
    margin: 0;
  }
  .branch_select_block{
    order:5;
  }
  .phone_block{
    order: 6;
  }
  .button.feedback_button{
    order:7;
    margin: 0 auto;
  }
  .icons_wrapper{
    order:2;
    display: none;
  }
  .menu_wrapper{
    display: none;
  }
}
@media (max-width: 478px) {
  .header_icon.top_burger{
    order: 2;
  }
  .search_block{
    order: 4;
  }
  .branch_select_block{
    order:5;
    margin: 0 auto;
  }
  .phone_block{
    order: 6;
    margin: 0 auto;
  }
  .button.feedback_button{
    order:7;
    width: 208px;
  }
  .icons_wrapper{
    order: 3;
    width: 100%;
    .icons_box{
      justify-content: space-between;
      width: 100%;
      .header_icon{
        margin-right: 0;
      }
    }
  }
  .menu_items{
    display: none;
  }
  .scroll_items{
    display: none;
  }
  .button.catalog_button{
    margin: 0 auto;
    width: 208px;
    padding-left: 95px;
  }
}