footer{
  background: #F9F9F9;
  padding: 97px 0 47px 0;
}
.bottom_mobile_menu{
  display: none;
}
.footer_top_block{
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  .phone{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 32px;
    line-height: 48px;
    color: #6701BA;
  }
}
.footer_header{
  font-size: 18px;
  line-height: 25px;
  color: #272727;
  font-weight: bold;
  margin-bottom: 15px;
}
.footer_about_company{
  order: 2;
  display: flex;
  flex-direction: column;
  margin: 0 10px;
}
.footer_customers{
  order:3;
  display: flex;
  flex-direction: column;
  margin: 0 10px;
}
.footer_subscribe_block{
  order: 4;
  max-width: 460px;
  margin-left: 10px;
}
.footer_item{
  font-size: 15px;
  line-height: 25px;
  color: #272727;
  margin-bottom: 10px;
}
.footer_subscribe_form{
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.subscribe_input{
  border: none;
  background: #FFFFFF;
  border-radius: 6px;
  padding: 0 15px;
}
.submit_wrap{
  position: relative;
  &::before{
    content: '';
    display: block;
    width: 54px;
    height: 40px;
    position: absolute;
    top: 5px;
    left: 11px;
    border-right: 1px solid #fff;
    background: url("../img/mail.svg") no-repeat center;
  }
}
.subscribe_button{
  width: 190px;
  margin-left: 10px;
  border: none;
  padding-left: 70px;
  padding-right: 32px;
}
.divide{
  height: 1px;
  background: #A8A8A8;
}
.footer_bottom_block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
}
.socials_icons_block{
  display: flex;
}
.social{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50px;
  border: 1px solid #82BA01;
  margin-right: 10px;
  &:hover{
    background: #82BA01;
    svg path{
      fill: #fff;
    }
  }
}
.social:last-child{
  margin-right: 0;
}
.copyrights{
  color: #A8A8A8;
  font-size: 15px;
  line-height: 25px;
}
@media (max-width: 1200px) {
  .footer_top_block{
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
  }
  .footer_about_company{
    margin: 0 15px;
  }
  .footer_subscribe_block{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    .footer_header{
      text-align: center;
    }
  }
}
@media (max-width: 768px) {
  .footer_top_block{
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .footer_subscribe_form{
    flex-direction: column;
    width: 100%;
    .subscribe_input{
      width: 70%;
      height: 50px;
      margin: 0 auto 15px auto;
    }
    .submit_wrap{
      width: 190px;
      margin: 0 auto;
      &:before{
        left: 0;
      }
      .subscribe_button{
        margin-left: 0;
      }
    }
  }
  .footer_bottom_block{
    flex-direction: column-reverse;
  }
  .socials_icons_block{
    margin-bottom: 30px;
  }
  footer{
    padding: 60px 0 105px 0;
  }
  .bottom_mobile_menu{
    display: flex;
    width: 100%;
    height: 75px;
    align-items: center;
    position: fixed;
    bottom: 0;
    z-index: 1001;
    a{
      width: 25%;
      height: 75px;
      align-items: center;
      justify-content: center;
      text-decoration: none;
      color: #fff;
      font-size: 12px;
      line-height: 20px;
      display: flex;
      flex-direction: column;
      background: #6701BA;
      &:active{
        background: #9933EC;
      }
    }
  }
}
@media (max-width: 478px) {
  .category_img{
    width: 100%;
  }
  .category_img{
    width: 100%;
  }
}