.contacts_page{
  padding: 30px 0 100px 0;
  .popular_category_header{
    margin-bottom: 30px;
  }
  .contacts_buttons{
    display: flex;
    margin-bottom: 30px;
    .contact_btn{
      width: 300px;
      border: 1px solid #6701BA;
      background: #fff;
      color: #272727;
      text-align: center;
      margin-right: 30px;
      &:hover{
        background: #9933EC;
        border: 1px solid #9933EC;
        color: #fff;
      }
      &.active{
        border: 1px solid #6701BA;
        background: #6701BA;
        color: #fff;
      }
    }
  }
  .map{
    width: 100%;
    height: 545px;
    margin-bottom: 30px;
  }
  .billets{
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    .billet{
      padding-left: 30px;
      padding-top: 30px;
      width: calc(25% - 20px);
      height: 140px;
      background: #F9F9F9;
      border-radius: 6px;
      span{
        font-size: 15px;
        line-height: 25px;
        color: #A8A8A8;
      }
    }
  }
  .billet_header{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    color: #272727;
  }
  .left_block, .right_block{
    width: calc(50% - 15px);
  }
  .props_block{
    display: flex;
    justify-content: space-between;
  }
  .props_header{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 26px;
    margin-bottom: 20px;
    line-height: 35px;
    color: #272727;
  }
  .props_item{
    padding: 15px;
    width: 100%;
    height: 55px;
    border-radius: 6px;
    display: flex;
    justify-content: space-between;
    span{
      font-size: 15px;
      line-height: 25px;
      color: #272727;
    }
  }
  .props_item:nth-child(2n+1){
    background: #F9F9F9;
  }
  .input_item{
    display: flex;
    flex-direction: column;
    label{
      font-size: 15px;
      line-height: 17px;
      color: #52575D;
    }
    textarea{
      resize: none;
      background: #F9F9F9;
      border-radius: 6px;
      border: none;
      padding-left: 15px;
      padding-top: 15px;
      margin-bottom: 20px;
    }
    textarea::-webkit-input-placeholder {
      color: #A8A8A8;
      font-size: 15px;
    }

    textarea:-moz-placeholder {
      color: #A8A8A8;
      font-size: 15px;
    }

    textarea:-ms-input-placeholder {
      color: #A8A8A8;
    }
  }
  .inputs_block{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    .input_item{
      width: calc(50% - 15px);
      input{
        height: 60px;
        background: #F9F9F9;
        border-radius: 6px;
        border: none;
        padding-left: 15px;
      }
      input::-webkit-input-placeholder {
        color: #A8A8A8;
        font-size: 15px;
      }

      input:-moz-placeholder {
        color: #A8A8A8;
        font-size: 15px;
      }

      input:-ms-input-placeholder {
        color: #A8A8A8;
      }
    }
  }
  .subscribe_button{
    padding-left: 75px;
    margin-left: 0;
  }
  .disclimer_block{
    display: flex;
  }
  .disclimer{
    font-size: 15px;
    line-height: 25px;
    color: #A8A8A8;
    margin-left: 30px;
    a{
      color: #6701BA;
    }
  }
}
@media (max-width: 1200px) {
  .contacts_page{
    padding: 30px 0 80px 0;
    .contacts_buttons{
      justify-content: space-between;
      .contact_btn{
        width: calc(30% - 15px);
        margin-right: 0;
      }
    }
    .billets{
      flex-wrap: wrap;
      margin-bottom: 60px;
      .billet{
        width: calc(50% - 10px);
        margin-bottom: 20px;
        text-align: center;
        padding-left: 0;
      }
    }
    .props_block{
      flex-direction: column;
      .left_block, .right_block{
        width: 100%;
      }
      .left_block{
        margin-bottom: 80px;
      }
    }
  }
}
@media (max-width: 768px) {
  .contacts_page{
    padding: 30px 0 0 0;
    .billets {
      .billet {
        width: 100%;
      }
    }
    .breadcrumbs_block{
      margin: 0;
    }
    .breadcrumb{
      display: none;
    }
    .contacts_buttons{
      flex-direction: column;
      margin-bottom: 10px;
      .contact_btn{
        width: 100%;
        margin-bottom: 20px;
      }
    }
    .inputs_block{
      flex-direction: column;
      margin-bottom: 0;
      .input_item{
        width: 100%;
        margin-bottom: 20px;
      }
    }
    .props_block{
      margin-bottom: 60px;
    }
    .disclimer_block{
      flex-direction: column-reverse;
      align-items: center;
      .disclimer{
        margin-left: 0;
        margin-bottom: 20px;
      }
      .submit_wrap{
        width: 280px;
        &::before{
          left:0;
        }
      }
      .subscribe_button{
        width: 280px;
      }
    }
  }
}