
/* Example wrapper */
.wrap {
  position: relative;
}

/* Frame */
.frame {
  height: 295px;
  line-height: 295px;
  overflow: hidden;
}
.frame ul {
  list-style: none;
  margin: 0;
  padding: 0;
  height: 100%;
  font-size: 50px;
}
.frame ul li {
  float: left;
  width: 300px;
  height: 255px;
  margin: 0 30px 0 0;
  padding: 0;
  background: #FFFFFF;
  box-shadow: 0 20px 30px -8px rgba(69, 78, 96, 0.15);
  border-radius: 6px;;
  color: #ddd;
  text-align: center;
  cursor: pointer;
}
.frame ul li.active {

}

/* Scrollbar */
.scrollbar {
  height: 5px;
  background: #EDF0F6;
  line-height: 0;
  width: 370px;
  margin: 0 auto;
}
.scrollbar .handle {
  width: 100px;
  height: 10px;
  border-radius: 5px;
  background: #82BA01;
  position: relative;
  top: -2px;
  cursor: pointer;
}
.scrollbar .handle .mousearea {
  position: absolute;
  top: -9px;
  left: 0;
  width: 100%;
  height: 20px;
}
.pages {
  list-style: none;
  margin: 20px 0;
  padding: 0;
  text-align: center;
}
.pages li {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin: 0 4px;
  text-indent: -999px;
  border-radius: 10px;
  cursor: pointer;
  overflow: hidden;
  background: #fff;
  box-shadow: inset 0 0 0 1px rgba(0,0,0,.2);
}
.pages li:hover {
  background: #aaa;
}
/* Controls */
.controls { margin: 25px 0; text-align: center; }

/* One Item Per Frame example*/
.oneperframe { height: 300px; line-height: 630px; }
.oneperframe ul li { width: 1290px; text-align: center}
.oneperframe ul li.active { background: #fff; color: #272727}
.promotion_block{
  .oneperframe{
    height: 630px;
  }
  .frame ul li{
    height: 630px;
  }
}
@media(max-width: 1280px) {
  .oneperframe ul li { width: 1110px; }
}
