.orders_page{
  padding: 30px 0 100px 0;
  .accordion{
    display: none;
  }
}
.wrapper{
  justify-content: space-between;
}
.orders_table{
  width: 100%;
  display: block;
  thead{
    display: block;
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    width: 100%;
    tr{
      display: flex;
      background: #F9F9F9;
      td{
        text-align: center;
        padding: 15px;
        width: 16.7%;
      }
    }
  }
  tbody {
    display: block;
    width: 100%;
    tr {
      width: 100%;
      display: flex;
      align-items: center;
      td {
        text-align: center;
        font-size: 15px;
        line-height: 25px;
        padding: 15px 0;
        display: block;
        width: 16.7%;
        span {
          display: block;
          color: #A8A8A8;
        }
        .details{
          color: #6701BA;
          display: block;
          width: 85px;
          border-bottom: 1px solid #82BA01;
          margin: 0 auto;
          &:hover{
            text-decoration: none;
          }
        }
      }
    }
    tr:nth-child(2n){
      background: #F9F9F9;
    }
  }
}
.right_block{
  flex-grow: 1;
}
@media(max-width: 992px) {
  .orders_page{
    padding: 30px 0 80px 0;
    .right_block{
      width: 100%;
    }
  }
}
@media(max-width: 768px) {
  .orders_page{
    .accordion{
      display: block;
    }
    .orders_table{
      display: none;
    }
    .accordion .card {
      border: none;
      border-radius: 6px;
      margin-bottom: 15px;
    }

    .accordion .card-header {
      padding: 0 15px;
      border: none
    }

    .accordion .card-header button {
      padding: 0;
      font-size: 16px;
      line-height: 48px;
      color: #272727;

      &:hover, &:focus {
        text-decoration: none;
      }
    }

    .accordion .card-header button::after {
      font-family: 'FontAwesome';
      content: '\f107';
      font-size: 24px;
      display: inline-block;
      margin-right: 5px;
      width: 16px;
      height: 16px;
      float: right;
      color: #A8A8A8;
    }

    .accordion .card-header button[aria-expanded="true"] {
      .filter_header{
        color: #6701BA;
      }
      &::after {
        font-family: 'FontAwesome';
        content: '\f105';
        float: right;
        font-size: 24px;
        display: inline-block;
      }
    }
    .filter_header{
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
    }
    .card-body{
      padding-left: 15px;
      padding-top: 15px;
      .type_header{
        font-family: "Poppins Bold", sans-serif;
        font-weight: bold;
        font-size: 15px;
        line-height: 25px;
        margin-bottom: 10px;
      }
      .type_value{
        font-size: 15px;
        line-height: 25px;
        color: #272727;
        margin-bottom: 15px;
        span{
          color: #A8A8A8;
        }
      }
      .details{
        color: #6701BA;
        display: block;
        width: 85px;
        border-bottom: 1px solid #82BA01;
        &:hover{
          text-decoration: none;
        }
      }
    }
  }
}