.news_page{
  padding: 30px 0 100px 0;
}
.breadcrumbs_block{
  margin-bottom: 40px;
}
.breadcrumb{
  padding: 0;
  background-color: transparent;
  .breadcrumb-item a{
    color: #A8A8A8;
    font-size: 15px;
    line-height: 25px;
    text-decoration: underline;
  }
  .breadcrumb-item+.breadcrumb-item{
    padding-left: 15px;
    &:before{
      content: '|';
      color: #A8A8A8;
    }
  }
  .breadcrumb-item.active{
    color: #6701BA;
  }
}
.news_wrapper{
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.left_subblock, .right_subblock{
  width: calc(50% - 25px);
}
.news_img{
  width: 100%;
  height: auto;
}
.news_text{
  color: #272727;
  font-size: 18px;
  line-height: 30px;
}
.hashtag{
  color: #82BA01;
  font-weight: bold;
  font-size: 15px;
  line-height: 25px;
  margin-right: 20px;
}
.date_wrapper{
  margin-bottom: 20px;
  .date{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    color: #A8A8A8;
  }
}
.buttons_news_wrap{
  display: flex;
  margin-top: 60px;
  .next_news{
    width: 300px;
    border: 1px solid #6701BA;
    background: #fff;
    text-align: center;
    color: #272727;
    &:hover{
      border: 1px solid #9933EC;
      background:#9933EC ;
      color: #fff;
    }
  }
  .prev_news{
    width: 300px;
    background: #F9F9F9;
    color: #A8A8A8;
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    text-align: center;
    margin-right: 30px;
  }
}
@media(max-width: 1200px) {
  .news_wrapper{
    flex-direction: column;
    &.reverse{
      flex-direction: column-reverse;
    }
    .left_subblock, .right_subblock{
      width: 100%;
    }
  }
  .buttons_news_wrap{
    justify-content: space-between;
    .prev_news{
      margin-right: 0;
    }
  }
  .news_text{
    font-size: 15px;
    line-height: 25px;
  }
}
@media (max-width: 768px) {
  .buttons_news_wrap{
    flex-direction: column;
    .prev_news{
      margin-bottom: 20px;
    }
  }
  .news_page{
    padding: 20px 0 60px 0;
  }
}