.profile_page {
  padding: 30px 0 100px 0;
}

.profile_menu {
  background: #F9F9F9;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
}

.wrapper {
  display: flex;
  align-items: flex-start;
}

.profile_block {
  display: flex;
  align-items: center;
  padding: 30px;
  font-size: 18px;
  line-height: 30px;
}

.profile_img {
  display: block;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-right: 15px;
}

.profile_list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;

  li {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    padding: 17px 30px 17px 30px;

    &.active {
      background: #82BA01;
      color: #fff;
    }
  }
}

.right_block {
  margin-left: 30px;

}

.profile_header {
  font-family: "Poppins Bold", sans-serif;
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 20px;
}

.input_item {
  display: flex;
  flex-direction: column;

  label {
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 0;
  }

  input[type="text"] {
    background: #F9F9F9;
    border-radius: 6px;
    border: none;
    padding: 15px;
  }
}

.radio_items {
  display: flex;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 20px;
}

.radio-item {
  margin-right: 30px;

  label {
    position: relative;
    cursor: pointer;
    padding-left: 25px;
    font-size: 15px;
    line-height: 25px;

    &::before {
      content: '';
      display: block;
      position: absolute;
      background: #A8A8A8;
      width: 16px;
      height: 16px;
      border-radius: 50%;
      left: 0;
      top: 3px;
    }

    &::after {
      position: absolute;
      top: 7px;
      left: 4px;
      background: 0 0;
      width: 8px;
      height: 8px;

      content: '';

      border-radius: 50%;
    }
  }

  input[type="radio"]:checked + label {
    &::before {
      background: #82BA01;
    }

    &::after {
      background: #272727;
    }
  }

  input[type="radio"] {
    display: none;
  }
}

.input_file {
  background: #F9F9F9;
  border-radius: 6px;
  height: 60px;
  position: relative;
  line-height: 60px;
  padding-left: 15px;

  &::-webkit-file-upload-button {
    position: absolute;
    right: 0;
    width: 77px;
    height: 60px;
    content: '';
    background: #82BA01;
    border: none;
    border-bottom-right-radius: 6px;
    border-top-right-radius: 6px;
    cursor: pointer;
    color: transparent;
  }
}

.add_file_block {
  position: relative;
  margin-bottom: 20px;

  label {
    display: block;
    position: absolute;
    top: 17px;
    right: 27px;
    font-size: 15px;
    line-height: 25px;
    color: #fff;
    cursor: pointer;
    z-index: 1;
  }
}

.add_file_header {
  font-size: 18px;
  line-height: 30px;
}

.button-save {
  padding-left: 47px;
  padding-right: 47px;
  border: none;
  font-size: 18px;
}

@media(max-width: 992px) {
  .profile_page {
    padding: 30px 0 80px 0;
  }
  .wrapper {
    flex-direction: column;
  }

  .profile_menu {
    width: 100%;
    margin-bottom: 40px;

    .profile_block {
      justify-content: center;
    }
  }

  .profile_list {
    display: flex;
    width: 100%;

    li {
      width: 25%;
      text-align: center;
    }
  }

  .right_block {
    margin-left: 0;
  }

  .name_block {
    margin-right: 10px;
  }

  .add_photo_block {
    margin-left: 10px;
  }

  .add_photo_form {
    display: flex;
  }

  .add_file_header {
    margin-bottom: 0;
  }

  .button-save {
    margin: 0 auto;
  }

  .input_item {
    input[type="text"] {
      height: 60px;
    }
  }

  .add_file_block label {
    right: 22px;
  }
}

@media(max-width: 768px) {
  .profile_page {
    padding: 30px 0 60px 0;
  }
  .breadcrumbs_block {
    display: none;
  }

  .right_block {
    width: 100%;
  }

  .profile_list {
    flex-direction: column;

    li {
      width: 100%;
    }
  }

  .profile_header {
    font-size: 26px;
    line-height: 35px
  }

  .add_photo_form {
    flex-direction: column;
    width: 100%;
    align-items: center;
  }

  .name_block {
    width: 100%;
    margin-right: 0;
  }

  .add_file_block {
    width: 100%;
  }

  .add_photo_block {
    margin-left: 0;
    width: 100%;
  }

  .input_file {
    width: 100%;
  }

  .radio_items {
    flex-direction: column;
    align-items: flex-start;
  }
}