.overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;

  width: 100%;
  height: 100%;

  background: rgba(0, 0, 0, .3);
}

.order_block {
  width: 90%;
  max-width: 1290px;
  height: 1029px;
  background: #fff;
  position: fixed;
  z-index: 1000;
  top: 5%;
  left: calc(50% - 645px);
  padding: 30px;
  overflow: hidden;
  border-radius: 6px;
  display: none;

  .order_row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .order_header {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
  }

  .order_cost {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
  }

  .product {
    height: auto;
  }

  .product_description {
    h3 {
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
    }

    .product_price {
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
      color: #272727;
    }

    .product_price_old {
      color: #6701BA;
      text-decoration: line-through;
      font-size: 15px;
      line-height: 25px;
    }
  }

  .order_count {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px
  }

  .order_cost {
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
  }

  .order_popup_table {
    border-bottom: 1px solid #000000;;
  }

  .summ_row {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    margin-top: 20px;
    margin-bottom: 30px;

    span {
      font-size: 15px;
      line-height: 35px;
      color: #A8A8A8;
      margin-right: 25px;
    }

    .order_summ {
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 26px;
      line-height: 35px
    }
  }

  .order_details {
    display: flex;

    .order_item {
      width: 14.25%;
      text-align: center;
    }

    .order_details_header {
      padding: 15px 0;
      background: #F9F9F9;
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 15px;
      line-height: 25px;
      white-space: normal;
    }

    .order_details_value {
      padding: 15px 0;

      span {
        display: block;
        color: #A8A8A8;
      }
    }
  }

  textarea {
    resize: none;
    width: 100%;
    border: 1px solid #A8A8A8;
    border-radius: 3px;
  }
}

.order-close, .actual-order-close {
  font-size: 0;

  position: absolute;
  top: 15px;
  right: 15px;

  width: 17px;
  height: 17px;
  margin: 0;

  cursor: pointer;

  border: none;
  outline: 0;
  background-color: transparent;

  &:active {
    border: 0;
    outline: 0;
  }
}

.order-close::before,
.order-close::after,
.actual-order-close::before,
.actual-order-close::after {
  position: absolute;
  top: 8px;
  right: -3px;

  width: 22px;
  height: 2px;

  content: '';

  border-radius: 1px;
  background-color: #000;
}

.order-close::before, .actual-order-close::before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}

.order-close::after, .actual-order-close::after {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.actual_orders {
  width: 90%;
  max-width: 1290px;
  height: 700px;
  background: #fff;
  position: fixed;
  z-index: 1000;
  top: calc(50% - 350px);
  left: calc(50% - 645px);
  padding: 30px;
  overflow: hidden;
  border-radius: 6px;
  display: none;

  .accordion {
    display: none;
  }

  .order_header {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
    margin-bottom: 20px;
  }
}

.order-popup-show {
  display: block;
}

@media(max-width: 1440px) {
  .order_block {
    left: 5%;
  }
  .actual_orders {
    left: 5%;
  }
}

@media(max-width: 992px) {
  .order_block {
    width: 98%;
    left: 1%;
    top: 2%;
    height: 1170px;

    .order_details {
      flex-wrap: wrap;

      .order_item {
        width: 20%;
      }
    }
  }
  .actual_orders {
    width: 98%;
    left: 1%;
    top: 2%;
    height: 1170px;
  }
}

@media (max-width: 768px) {
  .order_block {
    overflow-y: scroll;

    .order_row {
      flex-wrap: wrap;

      .order_image {
        width: 100%;
      }

      .order_description {
        width: 100%;
      }

      .summ_row {
      }
    }

    .order_cost {
      font-size: 26px;
      line-height: 35px;
    }

    .order_details {
      flex-direction: column;

      .order_item {
        width: 100%;
      }
    }
  }
  .actual_orders {
    overflow-y: scroll;
    height: 600px;
    .orders_table {
      display: none;
    }

    .accordion {
      display: block;
    }

    .accordion .card {
      border: none;
      border-radius: 6px;
      margin-bottom: 15px;
    }

    .accordion .card-header {
      padding: 0 15px;
      border: none
    }

    .accordion .card-header button {
      padding: 0;
      font-size: 16px;
      line-height: 48px;
      color: #272727;

      &:hover, &:focus {
        text-decoration: none;
      }
    }

    .accordion .card-header button::after {
      font-family: 'FontAwesome';
      content: '\f107';
      font-size: 24px;
      display: inline-block;
      margin-right: 5px;
      width: 16px;
      height: 16px;
      float: right;
      color: #A8A8A8;
    }

    .accordion .card-header button[aria-expanded="true"] {
      .filter_header {
        color: #6701BA;
      }

      &::after {
        font-family: 'FontAwesome';
        content: '\f105';
        float: right;
        font-size: 24px;
        display: inline-block;
      }
    }

    .filter_header {
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
    }

    .card-body {
      padding-left: 15px;
      padding-top: 15px;

      .type_header {
        font-family: "Poppins Bold", sans-serif;
        font-weight: bold;
        font-size: 15px;
        line-height: 25px;
        margin-bottom: 10px;
      }

      .type_value {
        font-size: 15px;
        line-height: 25px;
        color: #272727;
        margin-bottom: 15px;

        span {
          color: #A8A8A8;
        }
      }
    }
    .order_header{
      font-size: 26px;
      line-height: 35px;
      text-align: center;
    }
  }
}