.slider {
  position: relative;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;

  /*   ,     */
  .slider__wrapper {
    position: relative;
    overflow: hidden;
  }

  /*     */
  .slider__items {
    display: flex;
    transition: transform 0.6s ease;
  }

  /*    */
  .slider__item {
    flex: 0 0 100%;
    max-width: 100%;
    height: auto;
  }

  /*    ""  "" */
  .slider__control {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    z-index: 3;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background:#F9F9F9 url("../img/arrow_right_g.svg") no-repeat center;
    background-size: 14px 14px;
    &:hover{
      background:#82BA01 url("../img/arrow_right_w.svg") no-repeat center;
      background-size: 14px 14px;
    }
  }

  .slider__control:hover,
  .slider__control:focus {
    color: #959595;
    text-decoration: none;
    outline: 0;
  }

  /*    */
  .slider__indicators {
    bottom: 30px;
    z-index: 0;
    display: flex;
    justify-content: center;
    padding-left: 0;
    list-style: none;
    margin: 0;
  }

  .slider__indicators li {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 10px;
    height: 10px;
    border: 1px solid #82BA01;
    border-radius: 50%;
    margin-right: 3px;
    margin-left: 3px;
    text-indent: -999px;
    cursor: pointer;
    background-color: transparent;
    background-clip: padding-box;
    &:hover{
      background: #F9F9F9;
    }
  }

  .slider__indicators li.active {
    background-color: #82BA01;
  }
}
.slider__control_prev {
}
.slider__control_next{
  transform: rotate(180deg);
}

.slider_controls_wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  .slider_controls_block{
    margin: 0 10px;
  }
}
.projects_slider{
  .slider__indicators{
    bottom: -67px;
    li{
      border: 1px solid #959595;
    }
  }
  .slider__control{
    bottom: -85px;
    color: #959595;
  }
}
.partners_slider{
  overflow: visible;
  .slider__indicators{
    bottom: -65px;
    li{
      border: 1px solid #959595;
    }
  }
  .slider__control{
    bottom: -83px;
    color: #959595;
  }

}