.product_page {
  padding: 30px 0 100px 0;

  .product_wrapper {
    display: flex;
    justify-content: space-between;
    .product_image, .product_description{
      width: 49%;
    }
    .product_image{
      display: flex;
      justify-content: space-between;
    }
    .product_description{
      background: #F9F9F9;
      border-radius: 6px;
      padding: 30px;
    }
    margin-bottom: 60px;
  }
  .big_slider{
    width: 82%;
    position: relative;
    .flickity-button{
      display: none;
    }
    .flickity-page-dots{
      display: none;
    }
  }
  .small_slider{
    width: 15%;
    .flickity-button{
      background: #f9f9f9;
      border-radius: 50%;
      &:hover{
        background: #82ba01;
      }
    }
    .carousel-cell{
      border: 1px solid rgba(69, 78, 96, 0.15);
      border-radius: 3px;
      img{
        transform: rotate(-90deg);
      }
    }
  }
  .flickity-viewport{
    overflow: hidden;
  }
  .carousel-cell {
    background: #fff;
    counter-increment: carousel-cell;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      height: 100%;
      display: block;
    }
  }

  .carousel-main {
    .carousel-cell {
      width: 100%;
      height: 360px;
      font-size: 5rem;
    }
  }

  .carousel-nav {
    width: 300px;
    transform: rotate(90deg) translate(30px, -100%);
    transform-origin: left top;

    @media screen and (max-width: 1200px) {
      transform: rotate(90deg) translate(30px, -50%);
    }

    .carousel-cell {
      transform: rotate(-90deg);
      width: 80px;
      height: 80px;
      cursor: pointer;
      margin-right: 1rem;
      font-size: 1.4rem;

      /* selected cell */
      &.is-nav-selected {
        border: 2px solid #Ed2;
      }
    }

    .flickity-prev-next-button {
      width: 40px;
      height: 40px;

      &.previous {
        left: -40px;
      }

      &.next {
        right: -40px;
      }
    }
  }
  .product_planks{
    display: flex;
    margin-bottom: 15px;
    .discount{
      margin-right: 15px;
    }
    .new{
      margin-right: 15px;
    }
  }
  .price_wrapper{
    display: flex;
    margin-bottom: 20px;
    .left_block{
      margin-right: 30px;
    }
    .right_block{
      display: flex;
      align-items: center;
    }
  }
  .product_header{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
    margin-bottom: 20px;
  }
  .product_price_old{
    font-size: 16px;
    line-height: 24px;
    color: #6701BA;
    text-decoration: line-through;
  }
  .product_price{
    font-size: 36px;
    line-height: 50px;
    color: #272727;
  }
  .add_to_cart{
    margin-left: 20px;
    text-align: center;
  }
  .description_header{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 15px;
  }
  .description_text{
    list-style: none;
    padding: 0;
    li{
      font-size: 15px;
      line-height: 25px;
      color: #272727;
    }
  }
  .properties{
    td{
      font-size: 15px;
      line-height: 25px;
    }
    td:first-child{
      display: flex;
      width: 205px;
      align-items: baseline;
      &::after{
        content: ''; /*   */
        border-bottom: 1px dashed #ccc; /*   */
        flex-grow: 1; /*      */
        margin: 0 5px;
      }
    }
  }
  .frame {
    height: 425px;
    padding-bottom: 20px;
    ul li{
      height: 405px;
      padding: 20px;
      cursor: auto;
    }
  }
  .product{
    .product_price{
      font-size: 18px;
      line-height: 25px;
    }
    .product_price_old{
      font-size: 15px;
      line-height: 25px;
    }
  }
  .popular_category_header{
    font-family: "Poppins Bold", sans-serif;
    font-size: 26px;
    line-height: 35px;
    font-weight: bold;
  }
}
@media (max-width: 1200px) {
  .product_page{
    .product_wrapper{
      flex-direction: column;
      .product_image{
        width: 100%;
        margin-bottom: 80px;
      }
      .small_slider{
        display: none;
      }
      .big_slider{
        width: 100%;
        .flickity-page-dots{
          display: block;
          .dot{
            width: 10px;
            height: 10px;
            border: 1px solid #82ba01;
            background: #fff;
            &.is-selected{
              background: #82ba01;
            }
          }
        }
      }
      .product_description{
        width: 100%;
      }
    }
  }
}
@media (max-width: 768px) {
  .product_page{
    .breadcrumbs_block{
      display: none;
    }
    .price_wrapper{
      flex-direction: column;
      .left_block{
        margin-right: 0;
        margin-bottom: 20px;
      }
    }
    .product_header{
      font-size: 26px;
      line-height: 35px;
    }
    .pagespan{
      display: block;
    }
    .properties td:first-child{
      width: 150px;
    }
  }
}