.error_page {
  padding: 100px 0;

  .popular_category_header {
    display: block;
    margin: 0 auto 40px auto;
    text-align: center;
  }

  .error_advice {
    display: block;
    text-align: center;
    margin: 0 auto 50px auto;
    font-size: 18px;
    line-height: 30px;
    color: #A8A8A8;
  }

  .error_search_block {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    margin-bottom: 50px;

    .search_form {
      margin-right: 30px;
      width: 410px;

      .search_input {
        width: 100%;
      }
    }

    .detail_button {
      width: 300px;
    }
  }
}

@media(max-width: 1200px) {
  .error_page {
    padding: 80px 0;
    .error_search_block {
      flex-direction: column;
      align-items: center;
      .search_form {
        width: 300px;
        margin-bottom: 20px;
        margin-right: 0;
      }
    }
  }
}
@media (max-width: 768px) {
  .error_page {
    padding: 40px 0;
    .popular_category_header{
      margin: 0 auto 20px auto;
    }
    .error_advice{
      margin: 0 auto 30px auto;
    }
    .error_search_block {
      .search_form {
        width: 100%;
      }
      .detail_button {
        width: 100%;
      }
    }
  }
}