.catalog_page{
  padding: 30px 0 100px 0;
  .catalog_block{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .category_item{
      width: calc(25% - 22px);
      box-shadow: 0 20px 50px -8px rgba(69, 78, 96, 0.15);
      margin-bottom: 30px;
      span{
        text-align: center;
      }
    }
  }
  .frame {
    height: 425px;
    padding-bottom: 20px;
    ul li{
      height: 405px;
      padding: 20px;
      cursor: auto;
    }
  }
}
.pages{
  margin: 0 20px;
  li{
    width: 10px;
    height: 10px;
    border: 1px solid #82BA01;
    box-shadow: none;
  }
  li.active{
    background: #82BA01;
  }
}
.controls_wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  .btn{
    z-index: 3;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: #f9f9f9 url(../img/arrow_right_g.svg) no-repeat center;
    background-size: 14px 14px;
    &:focus{
      box-shadow: none;
    }
    &:hover{
      background: #82ba01 url(../img/arrow_right_w.svg) no-repeat center;
      background-size: 14px 14px;
    }
  }
  .btn.next{
    transform: rotate(180deg);
  }
}
.product{
  height: 405px;
  padding: 20px;
  display: flex;
  position: relative;
  flex-direction: column;
  .product_image_block{
    height: 200px;
    display: flex;
    justify-content: center;
  }
  h3{
    font-size: 18px;
    line-height: 30px;
    color: #272727;
    text-align: start;
    margin-bottom: 9px;
  }
  .product_price_old{
    font-size: 15px;
    line-height: 25px;
    color: #6701BA;
    text-decoration: line-through;
    text-align: start;
    margin-bottom: 4px;
  }
  .product_price{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
    color: #272727;
    text-align: start;
    margin-bottom: 15px;
  }
  .count_block{
    display: flex;
    justify-content: space-between;
  }
  .discount, .new, .hit{
    position: absolute;
  }
}
.add_to_cart{
  width: 120px;
  height: 40px;
  background: #6701BA;
  color: #fff;
  font-size: 18px;
  line-height: 40px;
  border-radius: 6px;
  &:hover{
    text-decoration: none;
  }
}
.cart_item_number{
  display: flex;
  .cart_item_text{
    width: 38px;
    height: 40px;
    font-size: 15px;
    text-align: center;
    border: none;
    border-image-width: 0;
    &:focus-visible{
      outline: none;
    }
  }
  .cart_minus{
    width: 38px;
    height: 40px;
    background: #EDF0F6;
    border-radius: 6px 0 0 6px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:hover{
      background: #82BA01;
      svg path{
        fill: #fff;
      }
    }
  }
  .cart_plus{
    width: 38px;
    height: 40px;
    background: #EDF0F6;
    border-radius: 0 6px 6px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    &:hover{
      background: #82BA01;
      svg path{
        fill: #fff;
      }
    }
  }
}
.discount{
  width: 60px;
  height: 30px;
  background: #BA2201;
  border-radius: 0 15px 15px 0;
  padding-left: 10px;
  color: #fff;
  font-size: 14px;
  line-height: 30px;
  top:20px;
  left: 0;
}
.new{
  width: 88px;
  height: 30px;
  background: #01BA13;
  border-radius: 0px 15px 15px 0px;
  padding-left: 10px;
  color: #fff;
  font-size: 14px;
  line-height: 30px;
  top:60px;
  left: 0;
}
.hit{
  width: 54px;
  height: 30px;
  background: #0177BA;
  border-radius: 0px 15px 15px 0px;
  padding-left: 10px;
  color: #fff;
  font-size: 14px;
  line-height: 30px;
  top: 100px;
  left: 0;
}
@media (max-width: 1200px) {
  .catalog_page{
    .catalog_block{
      .category_item {
        width: calc(50% - 15px);
      }
    }
  }
}
@media (max-width: 768px) {
  .catalog_page{
    .catalog_block{
      .category_item {
        width: 100%;
      }
    }
    .pagespan{
      display: block;
    }
  }
}