.wheel_section {
  overflow: hidden;
  width: 700px;
  position: relative;
  z-index: 1;
  margin-top: 90px;
  &::before{
    content: '';
    display: block;
    position: absolute;
    width: 700px;
    height: 198px;
    z-index: 8;
    bottom: -2px;
    left:-2px;
    background: url("../img/circle_back.webp") no-repeat center/contain;
  }
}
#rotate-carousel > li > a > div {
  text-indent: -9999px;
  position: absolute
}
.half-slider * {
  -webkit-backface-visibility: hidden
}
.half-slider {
  overflow: hidden;
  height: 530px;
  border-bottom: 1px solid rgba(0, 0, 0, .1);
  padding-top: 0;
  position: relative
}
.half-slider > .title {
  font: 600 62px/64px Roboto, Arial, Helvetica, sans-serif;
  text-align: center;
  text-transform: uppercase;
  position: relative;
  top: -144px
}
.rotate-slider {
  max-width: 562px;
  height: 530px;
  margin: 0 auto;
  display: block;
  position: relative;
  background: url(../img/ellipse.svg) no-repeat;
}
html.touch .rotate-slider {
  height: auto
}
.rotate-slider::-moz-selection {
  background: transparent;
  text-shadow: none
}
.rotate-slider::selection {
  background: transparent;
  text-shadow: none
}
.rotate-slider > span {
  display: inline-block;
  position: absolute;
  bottom: 23%;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #F9F9F9;
  cursor: pointer;
  background-size: cover;
  z-index: 12;
  &:hover{
    background: #82BA01;
    svg path{
      fill: #fff;
    }
  }
}
.rotate-slider > span:hover {
  opacity: 1
}
.rotate-slider > span.prev {
  left: -40px;
  transform: rotate(-90deg);
  display: flex;
  justify-content: center;
  align-items: center;
}
.rotate-slider > span.next {
  right: -40px;
  transform: rotate(-90deg);
  display: flex;
  justify-content: center;
  align-items: center;

}
#rotate-carousel {
  width: 562px !important;
  height: 562px !important;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  margin-top: 70px;
  position: relative;
  z-index: 1
}
#rotate-carousel > li {
  position: absolute;
  z-index: 8
}
#rotate-carousel > li > a {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  box-shadow: 0px 20px 50px -8px rgba(69, 78, 96, 0.15);
  border-radius: 50%;
  width: 93px;
  background-size: cover;
  cursor: pointer;
  height: 93px;
}
.circle_slider_img{
  height: 70%;
}
#rotate-carousel > li.item1 {
  top: -3px;
  left: 278px;
  margin-left: -40px;
  margin-top: -40px;
}
#rotate-carousel > li.item2 {
