.animate-holder {
  position: absolute;
  width: 280px;
  height: 280px;
  box-shadow: 0px 20px 50px -8px rgba(69, 78, 96, 0.15);
  border-radius: 50%;
  background: #fff;
  top: 130px;
  left: 140px;
  z-index: 10;

}

.animate-holder .title {
  display: block;
  font: 700 28px/30px Roboto, Arial, Helvetica, sans-serif;
  text-align: center;
  margin: -75px 150px 0;
