.slider-more-holder {
  text-align: center;
  display: block;
}
.slider-more {
  z-index: 10;
  display: block;
  width: 280px;
  height: 280px;
}
.slider-more, .slider-more:after {
  display: inline-block;
  position: relative
}
.slider-more:after {
  content: '';
  width: 8px;
  height: 20px;
  left: 10px;
  transition: left .1s;
}
.slider-more:hover {
  opacity: 1 !important
}