.news_list_page{
  padding: 30px 0 100px 0;
  .breadcrumbs_block{
    margin-bottom: 50px;
  }
  .news_header{
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
  }
  .news_wrapper{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-bottom: 40px;
    border-bottom: 1px solid #A8A8A8;
  }
  .show_more{
    display: none;
  }
  .news_item{
    width: 32%;
    box-shadow: 0px 20px 50px -8px rgba(69, 78, 96, 0.15);
    border-radius: 6px;
    height: auto;
    margin-bottom: 30px;
    .news_date{
      margin: 0 0 20px 0;
    }
    .description_block{
      padding: 20px;
      flex-grow: 1;
      .news_text{
        font-family: "Poppins Bold", sans-serif;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #272727;
      }
    }
  }
}
@media(max-width: 1200px) {
  .news_list_page{
    .news_wrapper{
      flex-direction: row;
      .news_item{
        width: 49%;
      }
    }
  }
}
@media(max-width: 768px) {
  .news_list_page {
    .news_wrapper {
      border-bottom: none;
      margin-bottom: 0;
      padding-bottom: 0;
      .news_item{
        width: 100%;
      }
    }
    .show_more{
      width: 105px;
      text-align: center;
      margin: 0 auto;
      display: block;
      font-size: 15px;
      line-height: 15px;
      height: 18px;
      color: #6701BA;
      text-decoration: none;
      border-bottom: 1px solid #B4EC33;
    }
    .bottom_block{
      display: none;
    }
  }
}