.section_page {
  padding: 30px 0 100px 0;

  .section_header {
    font-family: "Poppins Bold", sans-serif;
    font-weight: bold;
    font-size: 36px;
    line-height: 50px;
  }

  .back_block {
    width: 300px;
    height: 84px;
    display: flex;
    align-items: center;
    padding-left: 30px;
    border-bottom: 1px solid #A8A8A8;
  }
  .back {
    font-size: 15px;
    line-height: 25px;
    color: #272727;
    &::before {
      font-family: 'FontAwesome';
      content: '\f104';
      font-size: 20px;
      display: inline-block;
      float: left;
      margin-right: 10px;
    }

    &:hover {
      text-decoration: none;
    }
  }
  .header_row{
    .back{
      display: none;
    }
  }
  .wrapper {
    display: flex;

    .filter_block {
      width: 300px;
      background: #F9F9F9;
      border-radius: 6px;
      margin-right: 30px;
      padding-bottom: 30px;
      .categiries {
        padding-left: 30px;
        padding-right: 30px;
        margin-top: 35px;
        margin-bottom: 20px;
      }

      .section_header {
        font-family: "Poppins Bold", sans-serif;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
      }

      .filter_header {
        font-family: "Poppins Bold", sans-serif;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #272727;
      }
      .category_list {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
        li {
          margin: 10px 0;

          a {
            font-size: 18px;
            line-height: 30px;
            color: #272727;
          }
        }
      }

      .offers_block {
        padding-left: 30px;
        padding-right: 30px;
      }
      .offers_list {
        margin-top: 15px;
        padding-left: 0;
        list-style: none;

        li {
          display: flex;
          justify-content: space-between;
          align-items: center;
          position: relative;

          .checkbox-input {
            display: none;
            &:checked + label::after
            {
              border-color: #272727;

            }
            &:checked + label:before {
              background: #82BA01;
            }
          }

          label {
            padding-left: 26px;
            font-size: 18px;
            line-height: 30px;
          }

          label::before {
            width: 16px;
            height: 16px;
            content: '';
            display: block;
            position: absolute;
            border: 1px solid #A8A8A8;
            border-radius: 3px;
            left: 0;
            top: 7px;
            cursor: pointer;
          }
          label::after
          {
            position: absolute;
            top: 9px;
            left: 5px;

            width: 6px;
            height: 10px;

            content: '';
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);

            border-right: 3px solid transparent;
            border-bottom: 3px solid transparent;
          }
          span {
            font-size: 15px;
            line-height: 25px;
            color: #A8A8A8;;
          }
        }
      }
      .search_input{
        border: 1px solid #A8A8A8;
        width: calc(100% - 54px);
        background: #fff;
        height: 40px;
      }
      .reset_button{
        border: 1px solid #6701BA;
        background: #fff;
        color: #272727;
        width: 240px;
        margin: 30px auto;
        &:hover{
          border: 1px solid #9933EC;
          background: #9933EC;
          color: #fff;
        }
        &:focus{
          outline: none;
        }
        &:active{
          border: 1px solid #3F0092;
          background: #3F0092;
          color: #fff;
        }
      }
    }
  }

  .accordion {
    margin-left: 20px;
    margin-right: 20px;
  }

  .accordion .card {
    border: none;
  }

  .accordion .card-header {
    padding: 0;
    border: none
  }

  .accordion .card-header button {
    padding: 0;
    font-size: 16px;
    line-height: 48px;
    color: #272727;

    &:hover, &:focus {
      text-decoration: none;
    }
  }

  .accordion .card-header button::after {
    font-family: 'FontAwesome';
    content: '\f107';
    font-size: 24px;
    display: inline-block;
    margin-right: 5px;
    width: 16px;
    height: 16px;
    float: right;
    color: #A8A8A8;
  }

  .accordion .card-header button[aria-expanded="true"]::after {
    font-family: 'FontAwesome';
    content: '\f105';
    float: right;
    font-size: 24px;
    display: inline-block;
  }

  .accordion .card-body {
    padding: 10px 10px 40px 10px;
    color: #272727;
    background: #f9f9f9;
    ul li label {
      margin-bottom: 5px;
    }
    .scroll{
      height: 330px;
      overflow-y: scroll;
      &::-webkit-scrollbar {
        width: 5px;
        background-color: #A8A8A8;
      }

      &::-webkit-scrollbar-thumb {
        border-radius: 3px;
        background-color: #82BA01;
      }
    }
  }

  .ui-slider-horizontal {
    height: 3px;
    background: #A8A8A8;

    .ui-slider-range {
      background: #6701BA;
    }
  }

  .ui-slider .ui-slider-handle {
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: #82BA01;
    border: none;
    top: -7px;
  }

  .ui-widget.ui-widget-content {
    border: none;
  }

  .inputs_wrapper {
    display: flex;
    justify-content: space-between;
    margin-bottom: 25px;

    input[type="text"] {
      width: 110px;
      height: 40px;
      border: 1px solid #A8A8A8;
      border-radius: 6px;
      font-size: 18px;
      color: #272727;
      padding-left: 38px;
    }
    .input_item{
      position: relative;
    }
    label{
      display: block;;
      color: #A8A8A8;
      font-size: 15px;
      line-height: 25px;
      position: absolute;
      top: 7px;
      left: 10px;
    }
  }
  .section_content_block{
    width: calc(100% - 300px);
    z-index: 1;
    overflow: hidden;
  }
  .novelties_block{
    padding: 30px 0 30px 30px;
    background: #F9F9F9;
    border-radius: 6px;
    position: relative;
    z-index: 10;
    .novelties_header{
      font-family: "Poppins Bold", sans-serif;
      font-weight: bold;
      font-size: 18px;
      line-height: 25px;
      margin-bottom: 20px;
    }
  }
  .wrap{
    position: relative;
    z-index: 0;
  }
  .frame {
    height: 170px;
    overflow: visible;
    ul li {
      width: 190px;
      height: 140px;
      margin: 0 10px 0 0;
      display: flex;
      justify-content: center;
      flex-direction: column;
      position: relative;
      z-index: 1;
      padding: 20px;
      .image_block{
        display: flex;
        height: 100%;
      }
      &:hover .cell_description{
        display: block;
        top: 130px;
        background: #fff;
        z-index: 120;
      }
      .cell_description{
        width: 190px;
      }
    }
  }
  .scrollbar{
    position: relative;
  }
  .scrollbar, .handle, .mousearea{
    z-index: -1;
  }
  .selects_row{
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 0;
  }
  .selects{
    display: flex;
  }
  .count_select{
    background: #EDF0F6;
    border-radius: 6px;
    margin-right: 10px;
    .__select{
      width: 185px;
      height: 50px;
    }
  }
  .popularity_select{
    background: #EDF0F6;
    border-radius: 6px;
    margin-right: 10px;
    .__select{
      width: 180px;
      height: 50px;
    }
  }
  .price_select{
    background: #EDF0F6;
    border-radius: 6px;
    .__select{
      width: 216px;
      height: 50px;
    }
  }
  .__select{
    justify-content: center;
  }
  .__select__title{
    font-size: 15px;
    line-height: 25px;
  }
  .__select__title__arrow::after, .__select__title__arrow::before{
    width: 6px;
  }
  .__select__title__arrow::before{
    transform: translate(-2px,-50%) rotate(45deg);
  }
  .__select[data-state=active] .__select__title__arrow::before{
    transform: translate(-2px,-50%) rotate(-45deg);
  }
  .__select__title__arrow::after{
    transform: translate(2px,-50%) rotate(-45deg);
  }
  .__select[data-state=active] .__select__title__arrow::after{
    transform: translate(2px,-50%) rotate(45deg);
  }
  .__select__content{
    left: 0;
    width: 100%;
    box-shadow: none;
    top: 45px;
    background: #EDF0F6;
  }
  .__select__label{
    padding-left: 25px;
    padding-right: 0;
  }
  .filters{
    display: flex;
    .vertical, .horizontal{
      width: 48px;
      height: 48px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      cursor: pointer;
    }
    .active{
      background: #6701BA;
      svg path{
        stroke: #fff;
      }
    }
  }
  .filters_planks{
    display: flex;
    flex-wrap: wrap;
    margin-top: 20px;
    .filter_plank{
      height: 50px;
      padding: 0 15px;
      font-size: 15px;
      color: #fff;
      line-height: 50px;
      background: #82BA01;
      border-radius: 6px;
      margin-right: 10px;
      span{
        cursor: pointer;
      }
    }
    .reset_button{
      border: 1px solid #6701BA;
      background: #fff;
      color: #272727;
      font-size: 15px;
      padding: 0 15px;
      &:hover{
        border: 1px solid #9933EC;
        background: #9933EC;
        color: #fff;
      }
      &:focus{
        outline: none;
      }
      &:active{
        border: 1px solid #3F0092;
        background: #3F0092;
        color: #fff;
      }
    }
  }
  .products_wrapper{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 30px;
    border-bottom: 1px solid #A8A8A8;
  }
  .new, .discount, .hit{
    padding-left: 10px;
  }
  .product{
    box-shadow: 0px 20px 50px -8px rgba(69, 78, 96, 0.15);
    border-radius: 6px;
    width: 32%;
    background: #fff;
    height: auto;
    margin-bottom: 20px;
    .add_to_cart{
      border: 1px solid #6701BA;
      color: #272727;
      background: #fff;
      text-align: center;
      margin-left: 5px;
      &:hover{
        border: 1px solid #9933EC;
        background: #9933EC;
        color: #fff;
      }
    }
    .product_image_block {
      height: auto;
      img {
        width: 100%;
      }
    }
    &.product_horizontal{
      width: 49%;
      flex-direction: row;
      align-items: center;
      .product_description{
        width: 150px;
      }
      .product_image_block{
        display: block;
      }
      .count_block{
        flex-direction: column;
        .add_to_cart{
          margin-top: 10px;
          margin-left: 0;
        }
      }
    }
  }
  .filter_row, .show_more{
    display: none;
  }
}
.bottom_block{
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.show_more_button{
  background: #6701BA;
  color: #fff;
  font-size: 18px;
  padding: 0 15px;
  border: 1px solid #6701BA;
  &:hover{
    border: 1px solid #9933EC;
    background: #9933EC;
    color: #fff;
  }
  &:focus{
    outline: none;
  }
  &:active{
    border: 1px solid #3F0092;
    background: #3F0092;
    color: #fff;
  }
}
.paginator{
  display: flex;
  a{
    font-size: 18px;
    line-height: 40px;
    text-align: center;
    width: 37px;
    height: 40px;
    display: block;
    border-radius: 5px;
    color: #272727;
    &:hover{
      text-decoration: none;
    }
    &.current{
      background: #82BA01;
      color: #fff;
    }
  }
}
@media(max-width: 1200px) {
  .section_page{
    .filter_wrapper{
      display: none;
    }
    .filter_row{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 20px;
      .filter_wrap{
        display: flex;
        align-items: center;
      }
      .filter_icon{
        width: 56px;
        height: 56px;
        display: flex;
        background: #F9F9F9;
        border-radius: 50%;
        justify-content: center;
        align-items: center;
        margin-right: 10px;
        cursor: pointer;
        &:hover, &:active{
          svg path{
            stroke: #fff;
          }
          background: #82BA01;
        }
      }
      .filter_link{
        display: block;
        height: 30px;
        font-size: 18px;
        line-height: 30px;
        color: #6701BA;
        border-bottom: 1px solid #82BA01;
        &:hover{
          text-decoration: none;
        }
      }
    }
    .wrapper{
      flex-direction: column;
      .filter_block{
        width: 100%;
        margin-bottom: 20px;
        .inputs_wrapper{
          justify-content: flex-start;
          .input_item:first-child{
            margin-right: 10px;
          }
        }
        .back_block{
          display: none;
        }
        .categiries{
          margin-top: 0;
          padding-top: 20px;
        }
      }
      .section_content_block{
        width: 100%;
        .filters{
          display: none;
        }
      }
    }
  }
}
@media(max-width: 992px)  {
    .section_page{
      .product{
        width: 48%;
      }
    }
}
@media(max-width: 768px) {
  .section_page{
    padding: 30px 0;
    .breadcrumbs_block{
      display: none;
    }
    .products_wrapper{
      border-bottom: none;
    }
    .product{
      width: 100%;
    }
    .section_header{
      font-size: 26px;
      line-height: 35px;
    }
    .pagespan{
      display: block;
    }
    .selects{
      flex-direction: column;
      width: 100%;
    }
    .count_select {
      margin-right: 0;
      margin-bottom: 20px;
      .__select{
        width: 100%;
      }
    }
    .__select{
      justify-content: flex-start;
      padding: 0 15px;
    }
    .__select__title{
      width: 95%;
    }
    .popularity_select{
      margin-right: 0;
      margin-bottom: 20px;
      .__select{
        width: 100%;
      }
    }
    .price_select{
      .__select{
        width: 100%;
      }
    }
    .__select__label{
      padding-left: 15px;
    }
    .bottom_block{
      display: none;
    }
    .show_more{
      width: 105px;
      text-align: center;
      margin: 30px auto 0 auto;
      display: block;
      font-size: 15px;
      line-height: 15px;
      height: 18px;
      color: #6701BA;
      text-decoration: none;
      border-bottom: 1px solid #B4EC33;
    }
  }
}