.index_page{
  padding-top: 10px;
  overflow: hidden;

}
.gallery-cell {
  width: 190px;
  height: 140px;
  margin-right: 10px;
  background: #fff;
  border-radius: 6px;
  padding: 20px;
  position: relative;
  counter-increment: gallery-cell;
  box-shadow: 0 20px 50px -8px rgba(69, 78, 96, 0.15);
  z-index: 1;
  &:hover{
    .cell_description{
      display: block;
    }
  }
}

