  display: block;
  text-align: center;
  content: counter(gallery-cell);
  line-height: 200px;
  font-size: 80px;
  color: red;
}*/
.flickity-viewport{
  overflow: visible;
}
.flickity-button{
  background: #F9F9F9;
  &:hover{
    background: #82BA01;
    .flickity-button-icon {
      fill: #fff;
    }
  }
}
.flickity-prev-next-button{
  width: 40px;
  height: 40px;
}
.flickity-prev-next-button .flickity-button-icon{
  fill: #82BA01;
  left: 30%;
  top: 30%;
  width: 40%;
  height: 40%;
}
.cell_description{
  display: none;
  position: absolute;
  left: 0;
  background: #fff;
  box-shadow: 0 20px 50px -8px rgba(69, 78, 96, 0.15);
  padding: 20px;
  border-radius: 6px;
  z-index: 12;
}
.cell_description_text{
  color:#272727;
  font-size: 15px;
  line-height: 25px;
  margin-bottom: 5px;
}
.cell_price_block{
  display: flex;
  justify-content: space-between;
}
.cell_price_old{
  font-size: 15px;
  line-height: 25px;
  color: #6701BA;
  text-decoration: line-through;
}
.cell_price{
  font-weight: bold;
  font-size: 18px;
  line-height: 25px;
  color: #272727;
}
.add_to_cart_button{
  width: 48px;
  height: 48px;
  background: #F9F9F9;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  &:hover{
    background: #82BA01;
    svg{
      path{
        stroke: #fff;
      }
    }
  }
}
.main_slider{
  display: flex;
  justify-content: space-between;
  position: relative;
  &::before{
    content: '';
    display: block;
    position: absolute;
    width: 1170px;
    height: 1170px;
    z-index: -1;
    top: -638px;
    right: -228px;
    background: url("../img/circle.svg") no-repeat;
  }
}
.product_item{
  max-width: 440px;
  margin-top: 90px;
}
.product_price{
  color: #82BA01;
  font-size: 48px;
  line-height: 72px;
  font-family: "Poppins Bold", sans-serif;
  font-weight: bold;
}
.product_name{
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #272727;
  margin-bottom: 10px;
}
.product_type{
  font-size: 18px;
  line-height: 30px;
  color: #A8A8A8;
  margin-bottom: 10px;
}
.product_text{
  font-size: 15px;
  line-height: 25px;
  color: #272727;
  margin-bottom: 20px;
}
.buttons_wrap {
  display: flex;
  justify-content: space-between;
}
.to_cart_button, .detail_button{
  width: 190px;
  text-align: center;
}
.detail_button{
  border: 1px solid #6701BA;
  background: #fff;
  color: #272727;
  &:hover{
    border: 1px solid #9933EC;
  }
}
.category_block{
  background: #F9F9F9;
  padding: 100px 0;
}
.category_header_wrapper{
  display: flex;
  justify-content: space-between;
  align-items:center;
  margin-bottom: 30px;
}
.popular_category_header{
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 0;
  color: #272727;
}
.show_all{
  font-size: 15px;
  line-height: 15px;
  height: 18px;
  color: #6701BA;
  text-decoration: none;
  border-bottom: 1px solid #B4EC33;
}
.category_item{
  display: flex;
  height: 100%;
  flex-direction: column;
  &:hover{
    text-decoration: none;
  }
  span{
    color: #272727;
    font-size: 18px;
    line-height: 65px;
    font-weight: bold;
  }
}
.promotion_block{
  padding: 100px 0;
  .frame ul li {
    margin: 0 10px 0 0;
  }
}
.promotion_item{
  display: flex;
  justify-content: space-between;
  .product_image_wrapper{
    width: 740px;
    height: 630px;
    flex-shrink: 0;
    margin-left: 60px;
    overflow: hidden;
  }
  .promotion_product_image{
    width: 100%;
    height: auto;
  }
  .buttons_wrap{
    max-width: 410px;
  }
}
.promotion_description{
  display: flex;
  flex-direction: column;
  text-align: start;
}
.promotion_header{
  font-weight: bold;
  font-size: 26px;
  line-height: 35px;
  margin-bottom: 30px;
  color: #272727;
}
.promotion_text{
  font-size: 15px;
  line-height: 25px;
  color: #272727;
  margin-bottom: 30px;
}
.promotion_price_block{
  display: flex;
  align-items: flex-end;
  margin-bottom: 30px;
  .product_price{
    margin-right: 20px;
    line-height: 48px;
  }
  .product_price_old{
    font-size: 24px;
    line-height: 36px;
    color: #6701BA;
    text-decoration: line-through;
  }
}
.about_company_block{
  padding: 100px 0;
  background: #F9F9F9;
}
.about_company_header{
  font-weight: bold;
  font-size: 36px;
  line-height: 50px;
  color: #272727;
  margin-bottom: 30px;
}
.about_company_wrapper{
  display: flex;
  justify-content: space-between;
}
.about_company_text{
  width: 44%;
  font-size: 15px;
  line-height: 25px;
  margin-right: 30px;
}
.advantages_block{
  display: flex;
  justify-content: space-between;
  width: 630px;
  .advantage{
    width: 190px;
    height: 150px;
    padding-top: 79px;
    box-shadow: 0 20px 50px -8px rgba(69, 78, 96, 0.15);
    border-radius: 6px;
    text-align: center;
    font-weight: bold;
    font-size: 15px;
    line-height: 25px;
    position: relative;
    &.prices::before, &.delivery::before, &.skill::before{
      content: '';
      display: block;
      position: absolute;
      width: 48px;
      height: 48px;
      left: 71px;
      top: 21px;
    }
    &.prices::before{
      background: url("../img/adv_prices.svg") no-repeat center;
    }
    &.delivery::before{
      background: url("../img/adv_delivery.svg") no-repeat center;
    }
    &.skill::before{
      background: url("../img/adv_skill.svg") no-repeat center;
    }
  }
}
.news_block {
  padding: 100px 0;
  .frame {
    height: 475px;
    line-height: 475px;
    ul li {
      width: 410px;
      height: 435px;
    }
  }
}
.news_item{
  display: flex;
  height: 100%;
  flex-direction: column;
  &:hover{
    text-decoration: none;
  }
  .news_date{
    font-size: 15px;
    line-height: 25px;
    color: #A8A8A8;
    text-align: left;
    margin-top: 20px;
    margin-bottom: 10px;
    font-weight: normal;
    margin-left: 20px;
    margin-right: 20px;
  }
  span{
    color: #272727;
    font-size: 18px;
    line-height: 25px;
    font-weight: bold;
    text-align: left;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
  }
}
.slider_tablet{
  display: none;
}
.category_items, .news_items{
  display: none;
}
.promotion_block > .container > .show_all{
  display: none;
}
@media (min-width: 576px) {
  .flickity-prev-next-button{
    &.previous{
      left: calc(50% - 263px);
    }
    &.next{
      right: calc(50% - 263px);
    }
  }
}
@media (min-width: 768px) {
  .flickity-prev-next-button{
    &.previous{
      left: calc(50% - 345px);
    }
    &.next{
      right: calc(50% - 345px);
    }
  }
}
@media (min-width: 992px) {
  .flickity-prev-next-button{
    &.previous{
      left: calc(50% - 465px);
    }
    &.next{
      right: calc(50% - 465px);
    }
  }
}
@media (min-width: 1200px) {
  .flickity-prev-next-button{
    &.previous{
      left: calc(50% - 555px);
    }
    &.next{
      right: calc(50% - 555px);
    }
  }
}
@media (min-width: 1400px) {
  .flickity-prev-next-button{
    &.previous{
      left: calc(50% - 645px);
    }
    &.next{
      right: calc(50% - 645px);
    }
  }
}
@media(max-width: 1400px){
  .promotion_item{
    .product_image_wrapper{
      width: 50%;
      flex-shrink: 1;
    }
  }
  .promotion_description{
    width: 50%;
  }
}
@media(max-width: 1280px) {
  .promotion_item{
    .to_cart_button, .detail_button{
        width: 175px;
    }
  }

}
@media(max-width: 1200px) {
  .main_slider{
    display: none;
  }
  .slider_tablet{
    display: block;
    padding-bottom: 60px;
    padding-top: 60px;
  }
  .product_slide_item{
    display: flex;
  }
  .product_slide_description{
    margin-left: 20px;
  }
  .buttons_wrap{
    justify-content: space-around;
  }
  .promotion_item{
    flex-direction: column-reverse;
    .product_image_wrapper{
      width: 100%;
      height: 350px;
      margin-left: 0;
      margin-bottom: 60px;
    }
    .promotion_product_image{
      margin: -10% 0 0 0;
    }
    .buttons_wrap{
      justify-content: space-between;
    }
  }
  .promotion_description{
    width: 100%;
  }
  .promotion_block, .category_block, .about_company_block{
    padding: 80px 0;
  }
  .about_company_wrapper{
    flex-direction: column;
  }
  .about_company_text{
    width: 100%;
    margin-right: 0;
  }
  .advantages_block{
    width: 100%;
    margin-top: 40px;
  }
  .news_block .frame ul li{
    width: 335px;
    margin: 0 20px 0 0;
  }
}
@media (max-width: 768px) {
  .product_slide_description{
    margin-left: 0;
  }
  .product_slide_item{
    flex-direction: column;
    align-items: center;
  }
  .product_slide_image{
    width: 124px;
    height: 212px;
  }
  .product_price{
    font-size: 26px;
    line-height: 35px
  }
  .product_name{
    font-size: 18px;
    line-height: 25px;
  }
  .product_type{
    font-size: 15px;
    line-height: 25px;
  }
  .buttons_wrap{
    flex-direction: column;
    align-items: center;
  }
  .to_cart_button{
    margin-bottom: 20px;
  }
  .category_block{
    padding-top: 60px;
  }
  .pagespan{
    display: none;
  }
  .category_header_wrapper{
    flex-direction: column;
    align-items: center;
  }
  .popular_category_header{
    font-size: 26px;
    line-height: 35px;
    margin-bottom: 20px;
  }
  .category_items, .news_items{
    display: block;
  }
  .category_item{
    margin-bottom: 20px;
    span {
      text-align: center;
    }
  }
  .promotion_block {
    padding: 60px 0;
    .category_header_wrapper {
      .show_all {
        display: none;
      }
    }
  }
  .about_company_block, .category_block{
    padding: 60px 0;
  }
  .promotion_item .product_image_wrapper{
    margin-bottom: 20px;
    height: 300px;
  }
  .promotion_header{
    font-size: 18px;
    line-height: 25px;
    margin-bottom: 15px;
  }
  .promotion_price_block {
    margin: 0 auto 20px auto;
    .product_price{
      font-size: 36px;
      line-height: 50px;
    }
    .product_price_old{
      line-height: 50px;
    }
  }
  .promotion_block > .container{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .promotion_block > .container > .show_all{
    display: inline-block;
    margin: 30px auto 0 auto;
  }
  .about_company_header{
    font-size: 26px;
    line-height: 35px;
    text-align: center;
  }
  .advantages_block{
    margin-top: 30px;
    flex-direction: column;
    align-items: center;
    .advantage{
      margin-bottom: 20px;
    }
  }
  .news_items{
    margin-top: 20px;
  }
  .news_item{
    margin-bottom: 20px;
  }
  .category_header_wrapper{
    margin-bottom: 0;
  }
}
@media (max-width: 478px) {

  .to_cart_button, .detail_button, .promotion_item .to_cart_button, .promotion_item .detail_button {
    width: 100%;
  }
  .advantages_block{
    .advantage{
      width: 100%;
    }
    .advantage.prices::before, .advantage.delivery::before, .advantage.skill::before{
      left: calc(50% - 24px);
    }
  }
  .news_block{
    padding: 60px 0;
  }
}
